@import "../../style.scss";
.navBar{
    @include themify($themes){
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;
    height: 40px;
    border-bottom: 1px solid themed("border");
    position: sticky;
    top: 0;
    background-color: themed("bg");
    color:themed("textColor");
    z-index: 999;

    .left{
        display: flex;
        align-items: center;
        gap: 30px;

        span{
            font-weight: bold;
            font-size: 20px;
            color: themed("logo");
        }

        .search{
            display: flex;
            align-items: center;
            border: 1px solid themed("border");
            gap: 10px;
            border-radius: 5px;
            padding: 5px;
            input{
                border: none;
                width: 500px;
                background-color: transparent;
                color:themed("textColor");

                @include mobile{
                    display: none;
                }
            }
        }
    }

    .right{
        display: flex;
        align-items: center;
        gap: 20px;

        @include mobile{
            display: none;
        }
        .user{
            display: flex;
            align-items: center;
            gap: 10px;
            font-weight: 500;
            img{
                width: 30px;
                height: 30px;
                border-radius: 50%;
                object-fit: cover;
            }

            span{

            }
        }
    }
}
}